import React from "react";
import ReactGA from 'react-ga';

export default function AnalyticsLink(props) {
    return (
      <ReactGA.OutboundLink
        eventLabel={props.href}
        to={props.href}
        target="_blank"
        title={props.title}
        trackerNames={[props.href]}>
        {props.children}
      </ReactGA.OutboundLink>
    );
  };