import React, {useEffect} from "react";
import ReactGA from 'react-ga';
import ReactYouTube from 'react-youtube';

export default function Youtube(props) {
  useEffect(() => {
    ReactGA.event({
      category: 'YoutubePlayer',
      action: 'Appeared',
      value: props.id,
    });
  });

  const opts = {
    playerVars: {
      autoplay: false,
      },
    };
    return (
        <ReactYouTube {...props} opts={opts} videoId={props.id} containerClassName="YoutubePlayer" />
    );
  }
