import React, {useState, useEffect} from "react";
import ReactGA from 'react-ga';
import LazyImage from "../../../../components/Image";
import { Carousel } from 'react-responsive-carousel';

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "./styles.scss";

const getConfigurableProps = () => ({
  showArrows: true,
  showStatus: false,
  showIndicators: false,
  infiniteLoop: true,
  showThumbs: false,
  useKeyboardArrows: true,
  autoPlay: false,
  stopOnHover: false,
  swipeable: true,
  dynamicHeight: true,
  emulateTouch: true,
  thumbWidth: false,
  interval: 5000,
  transitionTime: 150,
  swipeScrollTolerance: 5,
});

const arrowStyles = {
    position: 'absolute',
    zIndex: 2,
    top: 0,
};

export default function ImageCarousel(props) {
    useEffect(() => {
        ReactGA.event({
            category: 'ImageCarousel',
            action: 'Appeared',
            value: props.id,
        });
    });

    function clickedCarousel(handler) {
        return () => {
            ReactGA.event({
                category: 'ImageCarousel',
                action: 'Click',
                value: props.id,
            });
            handler();
        };
    }
    return (
        <div className="ImageCarousel">
            <Carousel
                {...getConfigurableProps()}
                renderArrowPrev={(onClickHandler) => {
                    return (
                        <button type="button"
                            className="ArrowButton"
                            aria-label="Left Arrow on Carousel" onClick={clickedCarousel(onClickHandler)} style={{ ...arrowStyles, left: 0 }}>
                            {"<"}
                        </button>
                    )
                }}
                renderArrowNext={(onClickHandler) => {
                    return (
                        <button type="button"
                            className="ArrowButton"
                            aria-label="Right Arrow on Carousel" onClick={clickedCarousel(onClickHandler)} style={{ ...arrowStyles, right: 0 }}>
                            {">"}
                        </button>
                    )
                }}
            >
                {props.images.split(",").map(img => {
                    return (
                        <div className="CarouselImageWrapper" key={img}>
                            <LazyImage src={img} className="CarouselImage" alt="Carousel Image" />
                        </div>
                    );
                })}
            </Carousel>
        </div>
    );
}