import React, {useEffect, useState} from "react";
import { Link, graphql } from "gatsby";
import rehypeReact from "rehype-react";
import ReactGA from 'react-ga';
import { DiscussionEmbed } from "disqus-react";
import LazyLoad from 'react-lazyload';

import SEO from "../../components/SEO";
import Layout from "../../components/layout";
import Image from "../../components/Image";
import AmazonAd from "../../components/AmazonAd";
import AdsContainer from "../../components/AdsContainer";
import Youtube from './components/Youtube';
import AffiliateInfo from "./components/AffiliateInfo";
import ImageComparison from './components/ImageComparison';
import AnalyticsLink from './components/OutboundLink';
import ImageCarousel from "./components/ImageCarousel";
import Author from "../../components/Author";

import {createBlogPostingMetadata} from "../../jsonLd";

import "./styles.scss"; 

function Paragraph (data) {
  return <p className="Paragraph" {...data}>{data.children}</p>
};

function UnorderedList(props) {
  return <ul className="BlogList" {...props}>{props.children}</ul>
}

function OrderedList(props) {
  return <ol className="BlogList" {...props}>{props.children}</ol>
}

function Table(props) {
  return (
    <div className="Table">
        <table {...props}>{props.children}</table>
    </div>
  );
}

function AmazonAffiliate(props) {
  return (
    <div className="AmazonAffiliateLinksContainer">
      {props.children}
    </div>
  );
}

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: {
    "img": Image,
    "img-carousel": ImageCarousel,
    "youtube": Youtube,
    "img-compare": ImageComparison,
    "a": AnalyticsLink,
    "ul": UnorderedList,
    "ol": OrderedList,
    "amazon-affiliate": AmazonAffiliate,
    "amazon-ad": AmazonAd,
    "media-ads": AdsContainer,
    "table": Table,
    p: (props) => {
      const firstChild = props.children[0];
      if (typeof firstChild !== 'object') {
        return (
          <Paragraph>{props.children}</Paragraph>
        );
      }

      return props.children;
    },
  },
}).Compiler;

export default function Template({
  data
}) {
  const [start] = useState(new Date());
  const { relatedPosts, blog, site } = data;
  const { excerpt, fields, frontmatter, html, htmlAst, wordCount, id } = blog;

  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.event({
        category: 'TimeOnBlog',
        variable: 'time',
        value: diff, // in milliseconds
        label: id,
      });
    };
  });

  const disqusConfig = {
    shortname: "techwaifu",
    config: { identifier: id, title: frontmatter.title },
  };
  const coverPhoto = frontmatter.cover_photo.includes("imgur.com") && !frontmatter.cover_photo.endsWith(".jpg") ? `${frontmatter.cover_photo}.jpg` : frontmatter.cover_photo;

  const metadata = JSON.stringify({
    ...createBlogPostingMetadata({
      authorId: frontmatter.author || "mai",
      mainEntityOfPage: "True",
      headline: frontmatter.title,
      image: coverPhoto,
      datePublished: frontmatter.date,
    }),
    "genre": frontmatter.related_tag,
    "keywords": frontmatter.tags.join(", "),
    "wordcount": `"${wordCount.words}"`,
    "url": `${site.siteMetadata.siteUrl}/${id}`,
    "description": `"${excerpt}"`,
    "articleBody": `"${html}"`,
    "inLanguage": "en-US",
  });

  const adsData = [{
    id: "188970836", size: "300x250", id2: "188970836", isMobile: false
  }, {
    id: "188970836", size: "300x250", id2: "188970836", isMobile: true 
  }];

  return (
    <Layout adsData={adsData}>
      <SEO
        article={true}
        title={frontmatter.title}
        image={coverPhoto}
        description={excerpt}
        keywords={frontmatter.tags}
        urlPath={`${id}`}
        metadata={metadata}
      />
      <div className="blog-post-container">
        <section className="blog-post-content">
          <h1 className="Title">{frontmatter.title}</h1>
          <div className="Date">Published: {frontmatter.date}</div>
          <div className="AuthorBubbleCtr">
            <Author id={frontmatter.author} />
          </div>
          <div className="TimeToRead">{fields.readingTime.text}</div>
          <Image src={frontmatter.cover_photo} alt={frontmatter.title} />

          {renderAst(htmlAst)}
          {frontmatter.has_affiliate_links &&
            <AffiliateInfo />
          }
        </section>

        <section className="blog-post-tags">
          {frontmatter.tags.map((tag) => {
						const sanitizedTag = tag.toLowerCase().split(" ").join("_").trim();
            return (<Link key={tag} className="blogTag" to={`/blogs/${sanitizedTag}`}>#{tag}</Link>);
          })}
        </section>

        {relatedPosts.edges.length > 1 &&
          <section className="RelatedPostsCtr">
            <h2>Related Posts</h2>
            <div className="RelatedPosts">
              {relatedPosts.edges.map(({node}) => {
                const {cover_photo, slug, title} = node.frontmatter;
                return (
                  <Link className="RelatedPostCard" to={`/${slug}`}  key={slug}>
                    <article>
                      <div className="RelatedPostImgCtr">
                        <Image src={cover_photo} alt={title} />
                      </div>
                      <div className="RelatedPostTitleCtr">
                        {title}
                      </div>
                    </article>
                  </Link>
                );
              })}
            </div>
          </section>
        }

        <div className="CommentsSection">
          <LazyLoad height={200} offset={200}>
            <DiscussionEmbed {...disqusConfig} />
          </LazyLoad>
        </div>
      </div>

    </Layout>
  )
}

export const pageQuery = graphql`
  query($slug: String!, $relatedTag: String!) {
    site {
      siteMetadata {
        siteUrl
      }
    }
    blog: markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      htmlAst
      id
      excerpt
      frontmatter {
        author
        date(formatString: "MMMM D, YYYY")
        slug
        title
        tags
        cover_photo
        related_tag
        has_affiliate_links
        show_media_ads
      }
      wordCount {
        words
      }
      fields {
        readingTime {
          text
        }
      }
    }
    relatedPosts: allMarkdownRemark(
      limit: 3,
      filter: {
        frontmatter:{tags:{in: [$relatedTag]}, slug:{ne:$slug} }
      }
    ) {
      edges {
        node {
          frontmatter {
            slug
            title
            cover_photo
          }
        }
      }
    }
  }
`
