import React, { useEffect } from "react";
import ReactGA from 'react-ga';
import "./styles.scss";
import LazyLoad from 'react-lazyload';
import {
    useWindowWidth,
} from '@react-hook/window-size';

export default function AmazonAd({
    type
}) {
    const onlyWidth = useWindowWidth();
    useEffect(() => {
        if (type) {
            ReactGA.event({
                category: 'AmazonAd',
                action: 'Appeared',
                value: type,
                label: type,
            });
        }
    });

    let isMobile = onlyWidth <= 728;
    let src = "";
    let width = "";
    let height = "";
    if (isMobile) {
        src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=12&l=ez&f=ifr&linkID=9580ed829da785a9a0060260d0b209e3&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20"
        width = "300";
        height = "250";
    } else {
        src = "//rcm-na.amazon-adsystem.com/e/cm?o=1&p=48&l=ez&f=ifr&linkID=36e63d93fdc8e731f3ad45b0565fa04b&t=onew1ng3dang3-20&tracking_id=onew1ng3dang3-20"
        width = "728";
        height = "90";
    }

    if (src !== "") {
        return (
            <LazyLoad height={parseInt(height)} offset={parseInt(height)}>
                <div className="AmazonAdContainer">
                    <iframe src={src} width={width} height={height} scrolling="no" border="0" marginwidth="0" style={{ border: "none" }} frameborder="0"></iframe>
                </div>
            </LazyLoad>
        );
    }
    return null;
}
