import React, {useEffect} from "react";
import ReactGA from 'react-ga';
import ReactCompareImage from 'react-compare-image';

export default function ImageComparison(props) {
    useEffect(() => {
        ReactGA.event({
            category: 'ImageComparison',
            action: 'Appeared',
            value: props.id,
        });
    });

    return (
        <div className="ImageComparisonContainer">
            <ReactCompareImage
                {...props}
                leftImage={props.leftimage}
                leftImageAlt="Image Comparison"
                rightImage={props.rightimage}
                rightImageAlt="Image Comparison"
            />
        </div>
    );
}